import { useState } from "react";
import {
  CreateMatchesForRound16,
  CreateMatchesForQuarterfinals,
  CreateMatchesForSemifinals,
  CreateMatchesForFinal,
} from "../../util/CookieResultManager";
import Round from "./Round";
import "./Bracket.css";

const Bracket = (props) => {
  const r16 = CreateMatchesForRound16();
  const qf = CreateMatchesForQuarterfinals();
  const sf = CreateMatchesForSemifinals();
  const f = CreateMatchesForFinal();

  const [updatedR16, setR16] = useState(r16);
  const [updatedQf, setQf] = useState(qf);
  const [updatedSf, setSf] = useState(sf);
  const [updatedF, setF] = useState(f);

  return (
    <div className="bracket relative p-4 flex flex-nowrap flex-row m-auto">
      <Round
        name="Round of 16"
        class="rd16"
        matchups={updatedR16}
        setMatches={setR16}
        setNextRound={setQf}
        nextRoundMatches={updatedQf}
      />
      <Round
        name="Quarterfinals"
        class="quarterfinals"
        matchups={updatedQf}
        setMatches={setQf}
        setNextRound={setSf}
        nextRoundMatches={updatedSf}
      />
      <Round
        name="Semifinals"
        class="semifinals"
        matchups={updatedSf}
        setMatches={setSf}
        setNextRound={setF}
        nextRoundMatches={updatedF}
      />
      <Round name="Final" class="final" matchups={updatedF} setMatches={setF} />
    </div>
  );
};

export default Bracket;
