import React, { useState } from "react";
import Team from "../Team";
import PlaceHolderTeam from "../../util/PlaceHolderTeam";
import "./Match.css";

const Match = (props) => {
  const [match, setMatch] = useState(props.match);
  const selectedHandler = (team) => {
    const updatedMatch = { ...match };
    if (team.position === updatedMatch.team1.position) {
      team.selected = !team.selected;
      updatedMatch.team1 = team;
      if (team.selected && updatedMatch.team2.selected) {
        updatedMatch.team2.selected = !updatedMatch.team2.selected;
      }
    } else {
      team.selected = !team.selected;
      updatedMatch.team2 = team;
      if (team.selected && updatedMatch.team1.selected) {
        updatedMatch.team1.selected = !updatedMatch.team1.selected;
      }
    }

    console.log(props.matches);
    setMatch(updatedMatch);
    props.matches[match.id] = updatedMatch;
    console.log(props.matches);
    props.setMatches(props.matches);

    const updatedNextRound = [...props.nextRoundMatches];
    if (match.id % 2 === 0) {
      if (team.selected) {
        updatedNextRound[Math.floor(match.id / 2)].team1 = team;
      } else {
        updatedNextRound[Math.floor(match.id / 2)].team1 = PlaceHolderTeam;
      }
    } else {
      if (team.selected) {
        updatedNextRound[Math.floor(match.id / 2)].team2 = team;
      } else {
        updatedNextRound[Math.floor(match.id / 2)].team2 = PlaceHolderTeam;
      }
    }

    props.setNextRound(updatedNextRound);
  };

  const team1 =
    props.match && props.match.team1 ? props.match.team1 : PlaceHolderTeam;
  const team2 =
    props.match && props.match.team2 ? props.match.team2 : PlaceHolderTeam;

  return (
    <div className="match">
      <div className="teams border border-slate-300 rounded-tl-3xl rounded-br-3xl shadow-xl">
        <Team team={team1} onSelected={selectedHandler} size="small" />
        <Team team={team2} onSelected={selectedHandler} size="small" />
      </div>
    </div>
  );
};

export default Match;
